<script>
	import {
		applylist,
		applydelete,
		applyupdate,
		brandupdate
	} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					sitename: '',
					allprovinces: [],
					allcitys: [],
				},
				//
				sites: [],
				total: 0,
				page: 1,
				brand_fee : sessionStorage.brand_fee,
				PageNum: 1,
				listLoading: false,
				sels: [], //
				editFormVisible: false, //
				editLoading: false,
				editFormRules: {
					sitename: [{
						required: true,
						message: '10',
						trigger: 'blur'
					}],
				},
				//
				editForm: {
					sitename: '',
					more: '',
					allprovinces: [],
					allcitys: [],
					allareas: [],
				},

				addFormVisible: false, //
				addLoading: false,
				addFormRules: {

					sitename: [{
						required: true,
						message: '10',
						trigger: 'blur'
					}],

				},
				//
				addForm: {
					provinces: '',
					city: '',
					area: '',
					sitename: '',
					more: '',
					allprovinces: [],
					allcitys: [],
					allareas: [],
				}
			}
		},
		methods: {
			//pageNum
			handleCurrentChange(val) {
				this.pageNum = val
				this.getorders();
			},
			handleRemove(files) {},
			handlePreview(files) {},
			//
			getorders() {
				let para = {
					applyType: 1,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.sites = res.data.result.result;
					this.total = res.data.result.total;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			getphone() {
				let para = {
					applyType:1,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.newsits = res.data.result.result;
					let childArr = []
					this.newsits.forEach(value => {
						if (this.filters.telephone===value.buyerMobile){
							childArr.push(value)
						}
					})
					this.sites = childArr
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function(index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id
					};
					applydelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getorders();
					});
				}).catch(() => {

				});
			},
			shenheSubmit: function(index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id,
						score:2
					};
					applyupdate(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
					let parmas = {
						id: row.id,
						targetJson: {
						status:2
						}
					};
					brandupdate(parmas).then((res) => {
						console.log(res)
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
					});
						this.getorders();
					});
				}).catch(() => {

				});
			},
				selsChange: function(sels) {
					this.sels = sels;
				},
			},

			mounted() {
				this.getorders();
			}
			}</script>

<style scoped>
