<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span> </span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone"></el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					{{products.length}}
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="products" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" label="" width="90">
			</el-table-column>
			<el-table-column prop="headLogo" label="" width="50">
				<!--  -->
				<template slot-scope="scope">
					<img :src="scope.row.headLogo" height="30" width="30" />
				</template>
			</el-table-column>
			<el-table-column prop="name" label="" min-width="70">
			</el-table-column>
			<el-table-column prop="balance" label="" min-width="70">
			</el-table-column>
			<el-table-column label="" width="130">
				<template slot-scope="scope">
					<el-button  size="small"  type="primary"  @click="handleEdit(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
			<el-table-column prop="email" label="" min-width="120">
			</el-table-column>
			<el-table-column prop="mobile" label="" min-width="100">
			</el-table-column>
			<el-table-column prop="createTime" label="" min-width="130">
			</el-table-column>
			<el-table-column label="" width="90">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-dialog title="" :visible.sync="editFormVisible">
			<el-form :model="editForm" label-width="160px"  ref="editForm">
				<el-form-item prop="id" label="ID">
					<el-input v-model="editForm.id" auto-complete="off" :maxlength="10" placeholder="id"></el-input>
				</el-form-item>
				<el-form-item prop="balance" label="()">
					<el-input v-model="editForm.balance" auto-complete="off" :maxlength="10" placeholder="()"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit()" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20"  :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		getcustomerdDelete,
		getcustomerlist,
		getUseredite
	} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					name: '',
					headLogo: '',
					email: '',
					mobile: ''
				},
				editorOption: {},
				type: [],
				region: [],
				time: '',
				products: [],
				editFormVisible: false, //
				editLoading: false,
				total: 0,
				PageNum:1,
				listLoading: false,
				sels: [], //
				editForm: {
					balance: '',
				},
			}
		},
		methods: {
			//pageNum
			handleCurrentChange(val) {
				this.pageNum = val
				this.getProducts();
			},
			//
			getProducts() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
				};
				this.listLoading = true;
				//NProgress.start();
				getcustomerlist(para).then((res) => {
					//console.log(res)
					//this.PageNum = res.data.result.PageNum;
					//this.total = res.data.result.total;
					this.oldproducts = res.data.result.result;
					let oldchildArr = []
					this.oldproducts.forEach(value => {
						if ( value.vipFlag == 'N'){
							oldchildArr.push(value)
						}
					})
					this.products = oldchildArr
					this.listLoading = false;
					//NProgress.done();
				});
			},
			getphone() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10
				};
				this.listLoading = true;
				//NProgress.start();
				getcustomerlist(para).then((res) => {
					//this.total = res.data.result.total;
					this.pageNum= res.data.result.pageNum;
					this.oldproducts = res.data.result.result;
					let oldchildArr = []
					this.oldproducts.forEach(value => {
						if (typeof (value.vipFlag) == 'N'){
							oldchildArr.push(value)
						}
					})
					this.newproducts = oldchildArr
					let childArr = []
					this.newproducts.forEach(value => {
						if (this.filters.telephone===value.mobile){
							childArr.push(value)
						}
					})
					this.products = childArr
					console.log(this.products)
					this.listLoading = false;
					//NProgress.done();
				});
			},//
			handleEdit: function(index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			editSubmit: function(e) {
				console.log(e)
				this.$refs.editForm.validate((valid) => {
					if(valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							// let para = Object.assign({
							// 	balance: this.editForm.balance,
							// 	id:this.editForm.id
							// }, this.editForm);
							let para = {
								balance: this.editForm.balance,
								customerId:this.editForm.id
							};
							getUseredite(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getProducts();
							});
						});
					}
				});
			},
			//
			handleDel: function(index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id
					};
					getcustomerdDelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
			//
			batchRemove: function() {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					getcustomerlist(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			}
		},
		computed: {

		},

		components: {

		},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>



