import Router from 'vue-router'
import Vue from 'vue'

Vue.use(Router)

import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import echarts from './views/charts/echarts.vue'

//
import products from './views/nav1/products.vue'
import zhuanli from './views/nav1/zhuanli.vue'
import sbqggl from './views/nav1/sbqggl.vue'
import zlqggl from './views/nav1/zlqggl.vue'


//
import users from './views/nav2/users.vue'
import vipusers from './views/nav2/vipusers.vue'
import yjfk from './views/nav2/yjfk.vue'


//
import sb_order from './views/nav3/sb_order.vue'
import zl_order from './views/nav3/zl_order.vue'
import fw_order from './views/nav3/fw_order.vue'
import sqvipgl from './views/nav3/sqvipgl.vue'
import txgl from './views/nav3/txgl.vue'

//
import sbfenl from './views/nav4/sbfenl.vue'

//
import sbknow from './views/nav5/sbknow.vue'
import sbask from './views/nav5/sbask.vue'
import sbnew from './views/nav5/sbnew.vue'
import comnew from './views/nav5/comnew.vue'

//
import message from './views/nav6/message.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
//  //{ path: '/main', component: Main },
//  {
//      path: '/',
//      component: Home,
//      name: '',
//      iconCls: 'el-icon-s-operation',//class
//      leaf: true,//
//      children: [
//      { path: '/', component: echarts, name: '' },
//      ]
//  },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-goods',//class
        leaf: false,//
        children: [
        { path: '/', component: products, name: ''},
        { path: '/zhuanli', component: zhuanli, name: '' },
        { path: '/sbqggl', component: sbqggl, name: '' },
        { path: '/zlqggl', component: zlqggl, name: '' },
        //{ path: '/protype', component: protype, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-user',
        leaf: false,//
        children: [
            { path: '/users', component: users, name: '' },
            { path: '/vipusers', component: vipusers, name: 'VIP' },
            { path: '/yjfk', component: yjfk, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-tickets',
        leaf: false,//
        children: [
            { path: '/sb_order', component: sb_order, name: '' },
            { path: '/zl_order', component: zl_order, name: '' },
            { path: '/fw_order', component: fw_order, name: '' },
            { path: '/sqvipgl', component: sqvipgl, name: 'VIP' },
            { path: '/txgl', component: txgl, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-medal',
        leaf: true,//
        children: [
            { path: '/sbfenl', component: sbfenl, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-news',
        leaf: false,//
        children: [
            { path: '/sbknow', component: sbknow, name: '' },
            { path: '/sbask', component: sbask, name: '' },
            { path: '/sbnew', component: sbnew, name: '' },
            { path: '/comnew', component: comnew, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-postcard',
        leaf: true,//
        children: [
            { path: '/message', component: message, name: '' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    },
];

export default routes;



