<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span>  <i class="el-icon-location l-style"></i></span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone"></el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					{{sites.length}}
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="sites" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column prop="id" label="" width="80">
			</el-table-column>
			<el-table-column prop="description" label="" min-width="130">
			</el-table-column>
			<el-table-column prop="amount" label="" min-width="70">
			</el-table-column>
			<el-table-column prop="buyerMobile" label="" min-width="100">
			</el-table-column>
			<el-table-column prop="createTime" label="" min-width="130">
			</el-table-column>
			<el-table-column label="" width="90">
				<template slot-scope="scope">
					<el-button style="width: 60px;" type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
			<el-table-column label="" width="150">
				<template slot-scope="scope">
					<el-button v-show="scope.row.score =='1'" type="primary" size="small" @click="shenheSubmit(scope.$index, scope.row)"></el-button>
					<p v-show="scope.row.score =='0'"  size="small" style="color: #666"></p>
					<p v-show="scope.row.score =='2'"  size="small" style="color: #0ACA07"></p>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		applylist,
		applydelete,
		applyupdate
	} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					description: '',
					amount: '',
					buyerMobile: '',
					status:'',
					description:''
				},
				sites: [],
				total: 0,
				page: 1,
				PageNum: 1,
				listLoading: false,
				sels: [], //
			}
		},
		methods: {
			//pageNum
			handleCurrentChange(val) {
				this.pageNum = val
				this.getorders();
			},
			handleRemove(files) {},
			handlePreview(files) {},
			//
			getorders() {
				let para = {
					applyType:0,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.total = res.data.result.total;
					this.sites = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function(index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id
					};
					applydelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getorders();
					});
				}).catch(() => {

				});
			},
			getphone() {
				let para = {
					applyType:0,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.newsits = res.data.result.result;
					let childArr = []
					this.newsits.forEach(value => {
						if (this.filters.telephone===value.buyerMobile){
							childArr.push(value)
						}
					})
					this.sites = childArr
					this.listLoading = false;
					//NProgress.done();
				});
			},
			shenheSubmit: function(index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id,
						score:2
					};
					applyupdate(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getorders();
					});
				}).catch(() => {

				});
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
		},

		mounted() {
			this.getorders();
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>



